.NetNav{
    position: fixed;
    top:0;
    padding: 20px;
    width: 100vw;
    height: 30px;
    z-index: 10;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.NetNav .NetNav_contents{
    display: flex;
    justify-content: space-between;
    
}
 .navBlack{
    background-color: #111;
}
.NetNav .NetNav-logo{
    position: fixed;
    top: 0px;
    left: 20px;    
    object-fit: contain;
    width: 120px;
    cursor: pointer;
}

.NetNav .NetNav-avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    
}